const Noticia = (props) => {
    const { urlToImage, url, title, description, source } = props.noticia;
    let imagen;
    if (urlToImage !== '') {
        imagen = <img src={urlToImage} alt={title} className="card-img-top"></img>
    } else {
        imagen = <img src={'https://www.coettc.info/wp/wp-content/uploads/2018/02/coettc-bitcoin-burbuja-bombolla.jpg'} alt={title} className="card-img-top"></img>
    }
    return (
        <div className="col-md-6 col-12 mb-4">
            <div className="card">
                <div className="card-image">
                    {imagen}
                </div>
                <div className="card-body">
                    <h3 className="card-title">
                        {title}
                    </h3>
                    <p className="card-text">
                        {description}
                    </p>
                </div>
            </div>
        </div>
    )
}

export default Noticia
